<template>
  <div class="yao">
  	<div class="booo1">
  		<div class="bo1">
  		<ul>
  			<li><img src="../../assets/imgs/tao3_003_03.png"/></li>
  			<li><img src="../../assets/imgs/tao3_003_05.png"/></li>
  		</ul>
  	</div>
<div class="bo2">
  		<ul>
  			<router-link to="/"><li><img src="../../assets/imgs/yao_06.png"/></li></router-link>
  			<li>分享</li>
  			<li><img src="../../assets/imgs/yao_03.png"/></li>
  		</ul>
  </div>
  	</div>

  <div class="fen">
  	<ul>
  		<li><img src="../../assets/imgs/yao_11.png"/></li>
  		<li><img src="../../assets/imgs/yao_14.png"/></li>
  	</ul>
  </div>	
  </div>
  
</template>

<script>
	

export default {
  name: '',
  data () {
    return {

    }
   },
   mounted(){
    	$(document).ready(function(){
    	 $(".fen ul li img").mouseover(function(){
    $(this).css("transform","scale(1.1)");
  });
  $(".fen ul li img").mouseout(function(){
    $(this).css("transform","scale(1)");
  });
  })}
}
</script>
<style scoped>
	.booo1{height: 1.6rem;width: 100%;border-bottom: 0.02rem solid #e7e7e7;background-color: white;}
	.bo1{width: 100%;}
.bo1 img:first-child{
 width:1.14rem;height:0.37rem;
}
.bo1 img:last-child{
 width:1rem;height:0.37rem;
}
.bo1 ul{width: 90.13%;height:0.6rem;margin: 0 auto;display: flex;justify-content: space-between;align-items: center;}
.bo2{width: 92.5%;margin: 0 auto;margin-top: 0.2rem;}
.bo2 ul{display: flex;justify-content: space-between;}
.bo2 ul li:nth-child(1){width: 0.22rem;height: 0.36rem;}
.bo2 ul li:nth-child(1) img{width: 100%;height: 100%;}
.bo2 ul li:nth-child(2){font-size: 0.3rem;}
.bo2 ul li:nth-child(3){width: 0.42rem;height: 0.42rem;}
.bo2 ul li:nth-child(3) img{width: 100%;height: 100%;}
.fen{width: 100%;background-color: white;height: 14rem;}
.fen ul{width: 92.5%;margin: 0 auto;}
.fen ul li{width: 100%;}
.fen ul li img{width: 100%;border-radius: 0.2rem;}
.fen ul li:nth-child(1){padding-top: 0.44rem;}
.fen ul li:nth-child(2){padding-top: 0.2rem;}
</style>



