<template>
	<div class="www">
		<div class="body2" style="width: 100%;height: 1.02rem; ">
		<ul class="bod2" style="width: 100%;height: 1.02rem;position: fixed;bottom: 0; display: flex;text-align: center;align-items: center;justify-content: space-around;background-color: white;">
			<li><router-link to="/"><img src="../../assets/imgs/shou1.png"/></router-link><p style="color: red;">首页</p></li>
			<li><router-link to="/zai"><img src="../../assets/imgs/zai.png"/><p>在线客服</p></router-link></li>
            <li><router-link to="/yao"><img src="../../assets/imgs/yao.png"/><p>邀请好友</p></router-link></li>
            <li><router-link to="/ge"><img src="../../assets/imgs/ge.png"/><p>个人中心</p></router-link></li>
		</ul>
	</div>
	</div>
	
	
</template>

<script>
	export default{
		mounted(){
			$(function() {
		var da=[require("../../assets/imgs/shou.png"),require("../../assets/imgs/zai.png"),require("../../assets/imgs/yao.png"),require("../../assets/imgs/ge.png")]
		var dao=[require("../../assets/imgs/shou1.png"),require("../../assets/imgs/zai1.png"),require("../../assets/imgs/yao1.png"),require("../../assets/imgs/ge1.png")]
		$(".bod2 li").click(function  () {
			var ei=$(this).index();
			$(".bod2 li").eq(ei).find("img").attr("src",dao[ei]);	
		    $(".bod2 li").eq(ei).find("p").css("color","red");
		    for (let i=0;i<da.length;i++) {
		    	if (ei!=i) {
		    		$(".bod2 li").eq(i).find("img").attr("src",da[i]);
		    		$(".bod2 li").eq(i).find("p").css("color","#9f9f9f");
		    	}
		    	
		    }
		})
	})
		}
	}

</script>

<style scoped="scoped">
	.body2 li{height: auto;}
	.body2 img{padding-top:0.1rem;width: 0.44rem;height: 0.44rem;}
	.body2 li p{font-size: 0.2rem;color: #9f9f9f;}
</style>


