import Vue from 'vue'
import Router from 'vue-router'
import body from '@/components/home/body'
import zai from '@/components/zai/zai'
import yao from '@/components/yao/yao'
import ge from '@/components/ge/ge'
Vue.use(Router)

export default new Router({
  routes: [
    {
      	path:'/',
      component:body
    },
     {
      	path:'body',
      component:body
    },
   {
      	path:'/ge',
      component:ge
   },
    	{
      path:'/zai',
      component:zai
    },
    {
      	path:'/yao',
      component:yao
    }
  ]
})



